@import url(https://fonts.googleapis.com/css2?family=Trispace:wght@200;500&display=swap);
:root {
    --brown: #493A1C;
    --coral: #FF806C;
    --teal: #009790;
    --lightblue: #BDF6E9;
    --green: #78C29E;
  }
.wrapper {
    width: 100%;
    background: linear-gradient(0deg, rgb(255, 255, 255) 30%, rgba(189,246,233,1) 100%);
}
.cardHeader {
    background-color: #FF806C;
    background-color: var(--coral);
    font-family: 'Trispace', sans-serif;
    box-shadow: #009790 0px -2px 10px;
    box-shadow: var(--teal) 0px -2px 10px;
}
.footer {
    margin-top: 40px;
    border-top: 1px solid #493A1C;
    border-top: 1px solid var(--brown);
    padding: 30px;

}
.googleFont {
    font-family: 'Trispace', sans-serif;
    font-weight: 500;
    color: #493A1C;
    color: var(--brown);
}
.googleFont2 {
    font-family: 'Trispace', sans-serif;
    font-weight: 200;
    color: #493A1C;
    color: var(--brown);
    opacity: 60%;
}
.card {
    box-shadow: 0px 5px 8px #009790;
    box-shadow: 0px 5px 8px var(--teal);
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.heroContent {
  display: none;
}
